import React, {Component} from 'react';
import './App.css';
import './Compoenents/HeaderComponent';
import {Button} from "react-bootstrap";
import Light from "./Compoenents/LightComponent";

const server_path = "http://10.100.102.44:5000"
const dummy_server_path = "http://localhost:5000";

class App extends Component {
    constructor(props, context) {
        super(props, context);

        this.state = {
            data: "press get state",
            state: "off"
        }
        this.getRequest = this.getRequest.bind(this);
    }

    /**
     * Manage get requests to the server
     * Available posts requests:
     * /off
     * /on
     * /get_state
     *
     * @param path
     * @returns {Promise<void>}
     */
    getRequest = async (path) => {
        const response = await fetch(server_path + path);
        const body = await response.json()
            .then(res => res)
            .then(console.log())
        console.log(path + " was pressed")

        //only for the get_state response we need
        //to parse the answer
        if(path ==="/get_state"){
            if((body.express)==1){
                this.setState({state:"off"})
            }else if(body.express==0){
                this.setState({state:"on"})
            }
        }else{
            this.setState({state: body.express})
        }
    }

    render() {
        return (
            <div className="container">
                <div>
                    <Light state={this.state.state}/>
                </div>
                <div className='row'>
                    <div className='col-6'>
                        <Button className='controlButton' variant="success"
                                onClick={() => this.getRequest('/on')}>On</Button>
                    </div>
                    <div className='col-6'>
                        <Button className='controlButton' variant="danger"
                                onClick={() => this.getRequest('/off')}>Off</Button>
                    </div>
                </div>
                <div className='row'>
                    <div className='col-12'>
                        <Button className='controlButton' variant="dark"
                                onClick={() => this.getRequest('/get_state')}>Get
                            State</Button>
                    </div>
                </div>


            </div>
        );
    }
}

export default App;



